body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation{position:fixed;top:0;left:0;bottom:0;padding:0px;display:flex;flex-flow:row wrap;align-items:center;justify-content:center;width:14%;background-color:#2a4765;z-index:10000}.navigation .logo{display:flex;flex-flow:column}.navigation .logo .text{color:#fbfbfc;display:flex;flex-flow:column;justify-content:center;align-items:flex-start}.navigation .logo .text h4{margin:2px}.navigation .menu a{display:inline-block;padding:0 30px;font-size:1rem;color:#fbfbfc}.navigation .menu a:hover{color:tomato}.navigation .menu .active{color:tomato;font-weight:bold}



.onGoing{margin:40px 250px}.onGoing h1{margin:10px}.onGoing .coursBoxes{display:flex;flex-flow:row wrap;justify-content:space-between}

.courseBox{width:32%;height:300px;border-radius:10px;margin:10px 0;background-color:#e5e3e3;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseBox .imageBox{width:100%;height:50%;border-radius:10px 10px 0 0;background-color:white}.courseBox .imageBox img{justify-content:center;width:100%;height:100%;border-radius:10px 10px 0 0;object-fit:cover}.courseBox .newCourseBox{text-align:center;vertical-align:middle;background-color:#0f9814;color:white;height:25px;width:60px;margin:-35px 0 0 10px;position:relative;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseBox .newCourseBox h4{font-size:16px}.courseBox h2{color:#1f1f1f;float:left;font-size:1rem;margin:10px 0 0 10px}.courseBox p{color:#707070;margin:0 0 0 10px;font-size:0.8rem;float:left;text-align:left}



.assignments{margin:0px 200px}.assignments h1{float:left;margin-top:-35px;margin-left:-10px;font-size:30px;color:#1b1b1e}.assignments .assignmentboxes{display:flex;flex-flow:row wrap;justify-content:space-between}.quizes{margin:100px 200px}.quizes h1{float:left;margin-top:-40px;margin-left:-10px;font-size:30px;color:#1b1b1e}.quizes .quizboxes{display:flex;flex-flow:row wrap;justify-content:space-between}

.assignment h4{color:#1b1b1e;font-weight:1000;margin:10px 0 0 10px}.assignment .assignmentBox{width:250px;height:180px;margin:30px 0 10px;background-color:#e5e3e3;border-radius:10px 10px 10px 10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.assignment .assignmentBox .passed h2{float:left;margin:30px 82px 20px;color:#73A790}.assignment .assignmentBox .notPassed h2{float:left;margin:30px 60px 20px;color:#D7B17C}.assignment .assignmentBox .notSubmitted h2{float:left;margin:30px 42px 20px;color:#707070}.assignment .assignmentBox .submitted h2{float:left;margin:30px 64px 20px;color:#707070}.assignment .assignmentBox .info{margin:0px 30px 0px;color:#707070}.assignment .assignmentBox .info .duration{margin-top:20px}.assignment .assignmentBox .info .duration p{font-weight:100}

.quiz h4{color:#1b1b1e;font-weight:1000;margin:10px 0 0 10px}.quiz .quizBox{width:250px;height:180px;margin:30px 0 10px;background-color:#e5e3e3;border-radius:10px 10px 10px 10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.quiz .quizBox .submitted h2{float:left;margin:30px 70px 20px;color:#707070}.quiz .quizBox .notSubmitted h2{float:left;margin:30px 60px 20px;color:#707070}.quiz .quizBox .info{margin:0px 30px 0px;color:#707070}.quiz .quizBox .progress{margin-top:50px}.quiz .quizBox .progress p{font-weight:100}

.bar{position:fixed;top:0;right:0;bottom:0;background-color:#2a4765;width:14%;z-index:10000}

.app{text-align:center}

html{scroll-behavior:smooth}body{margin:0;padding:0;font-family:"Montserrat", sans-serif;background:#fbfbfc;background-size:100% auto;background-repeat:repeat-y}section{min-height:calc(100vh - 160px);padding:80px}h1{color:#1b1b1e;font-family:"Montserrat", sans-serif;font-weight:700;margin:0}h2,h3{font-family:"Montserrat", sans-serif;font-weight:700;margin:0;color:#fbfbfc}h4,h5,h6{font-family:"Montserrat", sans-serif;font-weight:500;margin:0}p{font-weight:500;margin:0;color:#707070;font-size:0.8rem}a{font-family:"Montserrat", sans-serif;font-weight:400;text-decoration:none;margin:0}

.coursePage-container{display:grid;grid-template-columns:auto auto auto auto auto auto;grid-gap:10px;background-color:#2196f3;padding:10px;margin:0 14%}.coursePage-container>div{text-align:center;padding:20px 0;font-size:30px}.coursePage-container .course-info{grid-column:1/ span 4}

.courseInfo{background-color:#e5e3e3;height:600px;padding:10px}.courseInfo h3{float:left;color:#1b1b1e;font-size:16}.courseInfo p{justify-content:left;float:left;width:70%}

